export default "\n/* header css */\n\n.techdashboard_header { height: auto; padding: 0; background: #fff; box-shadow: 0px 2px 7px 2px #b7b7b7; margin: 20px 0; }\n\n.techdashboard_header .header_wrapper{ width: 1400px; margin: 0 auto; display: -webkit-box; display: flex; -webkit-box-align: center; align-items: center; -webkit-box-pack: justify; justify-content: space-between;}\n\n.techdashboard_header .logo_wrapper { max-width: 160px; display: block; cursor: pointer; -webkit-box-flex: 0; flex: none; }\n\n.techdashboard_header .logo_wrapper:focus { outline: none; border: none; }\n\n.techdashboard_header .logo_wrapper img { max-width: 100%; vertical-align: middle; }\n\n.techdashboard_header .quotation { margin-bottom: 0px; }\n\n.techdashboard_header .quotation p { font-family: \"AvenirMedium\"; font-size: 22px; color: #002035; text-transform: uppercase; margin: 0; word-break: break-word; white-space: normal;\n  text-align: center; line-height: 22px;}\n\n.techdashboard_header .profile_section button { padding-top: 8px; }\n\n.techdashboard_header .profile_section { display: -webkit-box;  display: flex;  border-left: 1px solid #ada9a9;  padding-left: 5px;  margin-bottom: 0;\n  -webkit-box-pack: center;  justify-content: center;  -webkit-box-align: center;  align-items: center; }\n\n.techdashboard_header .profile_section .username { padding-right: 10px;\n  display: block;\n  font-family: \"AvenirRoman\";\n  font-size: 16px;\n  color: #1f1f1f;}\n\n.techdashboard_header .quotation .mat-list-base { display: -webkit-box; display: flex; padding-top: 0; }\n\n/* .techdashboard_header .quotation .mat-list-base .mat-list-item:before {\n  content: \"\";\n  left: -15px;\n  width: 2px;\n  height: 15px;\n  background: #1c1c1c;\n  display: inline-block;\n  position: absolute;\n  top: 35%;\n} */\n\n.techdashboard_header .quotation .mat-nav-list .mat-list-item { margin: 0; position: relative; font-family: \"AvenirMedium\"; font-size: 16px; color: #767676;}\n\n.active { color: #000000!important;  /* border-bottom: 3px solid #fecb00; position: relative; */}\n\n.active::before{ content: ''; width: 100%; height: 4px; background: #fecb00; position: absolute; bottom: -25px; }\n\n.togle_menu { display: none;}\n\n.techdashboard_header .header_title{ color: #000; font-family: 'AvenirMedium'; font-size: 18px; white-space: normal; word-break: break-word; text-align: center;\n  line-height: 20px; margin: 0; }\n\n@media screen and (max-width: 1500px){\n  .techdashboard_header .header_wrapper { width: 90%; }\n}\n\n@media screen and (max-width: 1199px){\n.techdashboard_header .quotation p { font-size: 18px;}\n.techdashboard_header .header_title { font-size: 16px; }\n\n}\n\n@media screen and (max-width: 991px){\n.techdashboard_header .header_wrapper { display: -webkit-box; display: flex; -webkit-box-orient: vertical; -webkit-box-direction: normal; flex-direction: column; }\n.techdashboard_header .quotation p { margin: 10px 5px; }\n.techdashboard_header .profile_section{ border-left: 0;}\n.togle_menu { display: block;}\n.success{ display: block;}\n.danger{ display: none;}\n.techdashboard_header .quotation .mat-list-base { display: -webkit-box; display: flex; -webkit-box-orient: vertical; -webkit-box-direction: normal; flex-direction: column; }\n.techdashboard_header .quotation { width: 100%;}\n.active::before{ bottom: 0; height: 2px;}\n.techdashboard_header .quotation .mat-nav-list .mat-list-item { display: -webkit-box; display: flex; -webkit-box-pack: center; justify-content: center; }\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"